@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
/* ================search bar ==================== */
.search-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 10px;
  width: 100%;
  position: relative;
}

.search-bar input {
  color: black;
  outline: none;
  border: none;
  height: 30px;
  /* width: 80%; */
  width: 100%;
  font-size: 16px;
  padding-left: 15px;
  border-radius: 20px;
}

/* ================results starts ==================== */
.results {
  padding-top: 0;
  margin-top: 0;
}
.results {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-gap: 10px;
  gap: 10px;
}

.results h1,
.results p {
  width: 200px;
}

.results-poster img,
.results-poster {
  max-width: 200px;
  height: 100%;
  border-radius: 5px;
  margin-right: auto;
  margin-left: auto;
  padding-bottom: 5px;
  transition: all 0.35s ease-in-out;
  width: 100%;
}
.results-poster img:hover {
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
}
/* ================results end ==================== */

/* ======================Media Queries ============ */

/* ================results results ==================== */
@media (max-width: 800px) {
  .results {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
  .results-poster img {
    max-width: 150px;
  }
}

@media (max-width: 414px) {
  .results {
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  }
  .results-poster img {
    max-width: 100px;
  }
}
/* ================results ends ==================== */

d/* ===== Details======= */
.details {
  /* background-color: red; */
  margin: auto;
  max-width: var(--max-width);
}

/* ===== Description starts====== */

.genres {
  margin-top: 20px;
}

.description .genres {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  align-items: flex-end;
}

.description div {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
}

.youtube-video {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  height: 0;
}

.youtube-video iframe {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute;
}
.youtube-and-description {
  display: grid;
  grid-template-columns: 1fr 1fr;

  grid-gap: 30px;

  gap: 30px;
}

.rating-container {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  align-items: flex-end;
}
/* ===== Description ends====== */

/* ============ Recommended starts============= */
.recommended {
  margin-top: 50px;
}

.recommended h1 {
  margin-bottom: 20px;
}

.recommended div {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-gap: 10px;
  gap: 10px;
}

.recommended img,
.recommended a {
  max-width: 200px;
  height: 100%;
  border-radius: 5px;
  margin-right: auto;
  margin-left: auto;
  padding-bottom: 5px;
  transition: all 0.35s ease-in-out;
  width: 100%;
}

.recommended img:hover {
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
}

/* ============ Recommended ends============= */

/* ======================Media Queries ================== */

@media (max-width: 800px) {
  /* =============== youtube video and description==== */
  .youtube-and-description {
    grid-template-columns: 1fr;
  }
  /* ============= Recommended =================== */
  .recommended div {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  }
  .recommended img,
  .recommended a {
    max-width: 150px;
  }
}

@media (max-width: 414px) {
  /* ============= Recommended =================== */
  .recommended div {
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  }
  .recommended img,
  .recommended a {
    max-width: 100px;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --max-width: 1200px;
}

.section {
  max-width: 1200px;
  max-width: var(--max-width);
  padding: 50px;
  margin: 0 auto;
}
body {
  background-color: black;
  color: white;
  font-family: "Poppins", sans-serif;
}

.scroll-container {
  display: inline-flex;
  width: 100%;
  margin: 0 auto;
  overflow-x: scroll;
}

.home-banner-wrapper {
  display: flex;
  align-items: center;

  width: 100vw;
  justify-content: space-between;
  max-width: var(--max-width);
  margin: 0 auto;
}

.home-banner-wrapper h1 {
  font-size: 60px;
  text-align: center;
}

.home-banner-wrapper p {
  font-size: 20px;
  text-align: center;
}
.home-banner-wrapper img {
  width: 100%;
  height: auto;
}

.home-banner {
  border-bottom: 8px solid #222;
  border-top: 8px solid #222;
}
.home-details {
  max-width: 500px;
}
/* ================== Media queries =================== */
@media only screen and (max-width: 1200px) {
  .home-banner-wrapper h1 {
    font-size: 45px;
  }
  .home-banner-wrapper p {
    font-size: 18px;
    padding: 0 90px;
  }
}

@media only screen and (max-width: 800px) {
  .home-banner-wrapper {
    flex-direction: column;
  }
  .home-banner-wrapper h1 {
    font-size: 40px;
  }
  .home-banner-wrapper p {
    max-width: 550px;
  }
  .home-banner-wrapper img {
    max-width: 500px;
  }
}

@media only screen and (max-width: 375px) {
  .home-banner-wrapper h1 {
    font-size: 30px;
  }
  .home-banner-wrapper p {
    font-size: 16px;
    padding: 0 30px;
  }
}

/* Hide scrollbar from all browsers starts */
/* Hide scrollbar from chrome */
.poster-wrapper::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.poster-wrapper {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}
/* Hide scrollbar from all browsers ends*/

.poster-wrapper img {
  width: 200px;
  border-radius: 5px;
  margin: 10px 10px 5px 0px;
  transition: all 0.35s ease-in-out;
}
.poster-wrapper .poster:nth-child(1) {
  margin-left: 10px;
}
.poster-wrapper img:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.category {
  margin-left: 10px;
}

/* scroll-container is working only through index.css*/
.scroll-container {
  display: inline-flex;
  width: 100%;
  margin: 0 auto;
  overflow-x: scroll !important;
}

.poster-container {
  margin: 50px 0px 10px;
}

@media only screen and (max-width: 800px) {
  .poster-container img {
    width: 150px;
  }
}

@media only screen and (max-width: 375px) {
  .poster-container img {
    width: 100px;
  }
}

